import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Container, Typography, Paper, Alert, Box, Button, Snackbar } from '@mui/material'
import { AppDispatch, RootState } from '@/store/store'
import {
  fetchDevices,
  deleteDevice,
  setDialogMode,
  setDialogOpen,
  setCurrentDevice,
  clearError,
  createDevice,
  updateDevice
} from '@/store/slices/deviceSlice'
import { Device } from '@/types/device'
import DeviceTable from '@/components/DeviceTable'
import LoadingSpinner from '@/components/common/LoadingSpinner'

const DeviceManagement: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>()
  const { devices, loading, error } = useSelector((state: RootState) => state.device)
  const [refreshTrigger, setRefreshTrigger] = React.useState(0)

  useEffect(() => {
    console.log(' ...')
    dispatch(fetchDevices())
  }, [dispatch, refreshTrigger])

  // 
  useEffect(() => {
    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        console.log(' ...')
        setRefreshTrigger(prev => prev + 1)
      }
    }

    document.addEventListener('visibilitychange', handleVisibilityChange)
    return () => document.removeEventListener('visibilitychange', handleVisibilityChange)
  }, [])

  const handleRetry = () => {
    console.log(' ...')
    dispatch(fetchDevices())
  }

  const handleEdit = (device: Device) => {
    console.log(' :', device)
    console.log('  proxyid:', device.proxyid)
    dispatch(setCurrentDevice(device))
    dispatch(setDialogMode('update'))
    dispatch(setDialogOpen(true))
  }

  const handleDelete = (device: Device) => {
    console.log(' :', device)
    console.log('  proxyid:', device.proxyid)
    if (!device.proxyid) {
      console.error('  proxyid ')
      return
    }
    dispatch(deleteDevice(device.proxyid))
  }

  const handleToggleEnable = (device: Device) => {
    console.log(' :', device)
    //  action
    // dispatch(toggleDeviceEnable(device.proxyid))
  }

  const handleCreate = () => {
    console.log(' ')
    dispatch(setCurrentDevice(null))
    dispatch(setDialogMode('create'))
    dispatch(setDialogOpen(true))
  }

  const handleCloseError = () => {
    dispatch(clearError())
  }

  const handleRefresh = () => {
    console.log(' ...')
    setRefreshTrigger(prev => prev + 1)
  }

  return (
    <Container maxWidth="xl" sx={{ mt: 2, mb: 4 }}>
      <Paper
        sx={{
          p: 4,
          borderRadius: 2,
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
        }}
      >
        <Box sx={{ mb: 4 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
            <Box>
              <Typography
                variant="h4"
                gutterBottom
                sx={{
                  fontWeight: 600,
                  background: 'linear-gradient(45deg, #1976d2 30%, #42a5f5 90%)',
                  backgroundClip: 'text',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',
                  mb: 1,
                }}
              >
                
              </Typography>
              <Typography variant="body1" color="text.secondary">
                
              </Typography>
            </Box>
            <Button
              variant="outlined"
              onClick={handleRefresh}
              disabled={loading}
              sx={{ borderRadius: 2 }}
            >
              
            </Button>
          </Box>
        </Box>

        {error && (
          <Alert
            severity="error"
            sx={{ mb: 3, borderRadius: 2 }}
            onClose={handleCloseError}
          >
            <Typography variant="body2">
              : {error}
            </Typography>
            <Box sx={{ mt: 1 }}>
              <Button size="small" onClick={handleRetry} variant="outlined">
                
              </Button>
            </Box>
          </Alert>
        )}

        {loading ? (
          <LoadingSpinner message="..." />
        ) : devices && Array.isArray(devices) && devices.length > 0 ? (
          <DeviceTable
            devices={devices}
            onEdit={handleEdit}
            onDelete={handleDelete}
            onToggleEnable={handleToggleEnable}
            onCreate={handleCreate}
          />
        ) : !error ? (
          <Box
            sx={{
              textAlign: 'center',
              py: 8,
              bgcolor: 'grey.50',
              borderRadius: 2,
            }}
          >
            <Typography variant="h6" color="text.secondary" gutterBottom>
              
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
              
            </Typography>
            <Button
              variant="contained"
              startIcon={<Button></Button>}
              onClick={handleCreate}
              sx={{
                borderRadius: 2,
                px: 4,
                background: 'linear-gradient(45deg, #1976d2 30%, #42a5f5 90%)',
              }}
            >
              
            </Button>
          </Box>
        ) : null}
      </Paper>

      {/*  Snackbar */}
      <Snackbar
        open={!!error}
        autoHideDuration={6000}
        onClose={handleCloseError}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Alert
          onClose={handleCloseError}
          severity="error"
          sx={{ width: '100%' }}
        >
          {error}
        </Alert>
      </Snackbar>
    </Container>
  )
}

export default DeviceManagement