import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import {
  Box,
  Paper,
  Typography,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Button,
  Chip,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Collapse,
  Alert,
} from '@mui/material'
import {
  ExpandMore as ExpandMoreIcon,
  BugReport as BugIcon,
  Refresh as RefreshIcon,
  Close as CloseIcon,
} from '@mui/icons-material'
import { RootState } from '@/store/store'

interface DebugPanelProps {
  onClose?: () => void
}

const DebugPanel: React.FC<DebugPanelProps> = ({ onClose }) => {
  const [expanded, setExpanded] = useState<string | false>('redux')
  const [logs, setLogs] = useState<string[]>([])

  const { devices, loading, error, currentDevice, dialogMode, dialogOpen } = useSelector(
    (state: RootState) => state.device
  )

  // 
  const addLog = (message: string, type: 'info' | 'error' | 'success' = 'info') => {
    const timestamp = new Date().toLocaleTimeString('zh-TW')
    const logMessage = `[${timestamp}] ${message}`
    setLogs(prev => [...prev.slice(-49), logMessage]) //  50 

    // 
    console.log(` ${message}`)
  }

  //  Redux 
  React.useEffect(() => {
    addLog(`Redux  - : ${devices?.length || 0}`, 'info')
    if (devices && devices.length > 0) {
      addLog(`: ${JSON.stringify(devices[0], null, 2)}`, 'info')
    }
  }, [devices])

  React.useEffect(() => {
    if (error) {
      addLog(`: ${error}`, 'error')
    }
  }, [error])

  React.useEffect(() => {
    if (currentDevice) {
      addLog(`: proxyid=${currentDevice.proxyid}`, 'success')
    } else {
      addLog('', 'info')
    }
  }, [currentDevice])

  const handleAccordionChange = (panel: string) => (event: React.SyntheticEvent, isExpanded: boolean) => {
    setExpanded(isExpanded ? panel : false)
  }

  const handleRefresh = () => {
    addLog('', 'info')
    window.location.reload()
  }

  const handleClearLogs = () => {
    setLogs([])
    addLog('', 'info')
  }

  return (
    <Paper
      sx={{
        position: 'fixed',
        top: 20,
        right: 20,
        width: 500,
        maxHeight: '80vh',
        zIndex: 9999,
        overflow: 'hidden',
        border: '2px solid #1976d2',
      }}
    >
      {/*  */}
      <Box
        sx={{
          p: 2,
          bgcolor: 'primary.main',
          color: 'white',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
        }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <BugIcon />
          <Typography variant="h6"></Typography>
        </Box>
        <Box>
          <IconButton size="small" onClick={handleRefresh} sx={{ color: 'white', mr: 1 }}>
            <RefreshIcon />
          </IconButton>
          {onClose && (
            <IconButton size="small" onClick={onClose} sx={{ color: 'white' }}>
              <CloseIcon />
            </IconButton>
          )}
        </Box>
      </Box>

      <Box sx={{ maxHeight: '70vh', overflow: 'auto' }}>
        {/* Redux  */}
        <Accordion expanded={expanded === 'redux'} onChange={handleAccordionChange('redux')}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography variant="subtitle1">Redux </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Box sx={{ mb: 2 }}>
              <Typography variant="body2" gutterBottom>
                <strong>:</strong> {loading ? '...' : ''}
              </Typography>
              <Typography variant="body2" gutterBottom>
                <strong>:</strong> {devices?.length || 0}
              </Typography>
              <Typography variant="body2" gutterBottom>
                <strong>:</strong> {error || ''}
              </Typography>
              <Typography variant="body2" gutterBottom>
                <strong>:</strong> {dialogMode}
              </Typography>
              <Typography variant="body2" gutterBottom>
                <strong>:</strong> {dialogOpen ? '' : ''}
              </Typography>
            </Box>

            {currentDevice && (
              <Alert severity="info" sx={{ mb: 2 }}>
                <Typography variant="body2">
                  <strong>:</strong> proxyid={currentDevice.proxyid}, IP={currentDevice.proxy_ip}
                </Typography>
              </Alert>
            )}
          </AccordionDetails>
        </Accordion>

        {/*  */}
        <Accordion expanded={expanded === 'devices'} onChange={handleAccordionChange('devices')}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography variant="subtitle1"></Typography>
          </AccordionSummary>
          <AccordionDetails>
            {devices && devices.length > 0 ? (
              <TableContainer>
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell>Proxy ID</TableCell>
                      <TableCell> IP</TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {devices.map((device) => (
                      <TableRow key={device.proxyid}>
                        <TableCell>
                          <Chip
                            label={device.proxyid}
                            size="small"
                            color={device.proxyid ? 'primary' : 'error'}
                          />
                        </TableCell>
                        <TableCell>{device.proxy_ip}</TableCell>
                        <TableCell>{device.Controller_type}</TableCell>
                        <TableCell>
                          <Chip
                            label={device.enable === 1 ? '' : ''}
                            color={device.enable === 1 ? 'success' : 'default'}
                            size="small"
                          />
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
            ) : (
              <Typography variant="body2" color="text.secondary">
                
              </Typography>
            )}
          </AccordionDetails>
        </Accordion>

        {/*  */}
        <Accordion expanded={expanded === 'logs'} onChange={handleAccordionChange('logs')}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography variant="subtitle1"></Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Box sx={{ mb: 2 }}>
              <Button size="small" onClick={handleClearLogs} variant="outlined">
                
              </Button>
            </Box>
            <Box
              sx={{
                bgcolor: 'grey.900',
                color: 'grey.100',
                p: 2,
                borderRadius: 1,
                fontFamily: 'monospace',
                fontSize: '0.75rem',
                maxHeight: 300,
                overflow: 'auto',
              }}
            >
              {logs.length > 0 ? (
                logs.map((log, index) => (
                  <Typography key={index} variant="body2" sx={{ mb: 0.5 }}>
                    {log}
                  </Typography>
                ))
              ) : (
                <Typography variant="body2" color="grey.400">
                  
                </Typography>
              )}
            </Box>
          </AccordionDetails>
        </Accordion>

        {/* API  */}
        <Accordion expanded={expanded === 'api'} onChange={handleAccordionChange('api')}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography variant="subtitle1">API </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography variant="body2" gutterBottom>
              <strong>:</strong> http://127.0.0.1:5200
            </Typography>
            <Typography variant="body2" gutterBottom>
              <strong>:</strong> http://127.0.0.1:8080
            </Typography>
            <Typography variant="body2" gutterBottom>
              <strong>API :</strong> /DeviceServiceConfig
            </Typography>
            <Alert severity="info" sx={{ mt: 1 }}>
               Network  API 
            </Alert>
          </AccordionDetails>
        </Accordion>
      </Box>
    </Paper>
  )
}

export default DebugPanel